import { Component } from '@angular/core';
import { ViewController,IonicPage,  NavController,Events , ActionSheetController, NavParams } from 'ionic-angular';
import {   FormBuilder,  FormGroup,   Validators } from '@angular/forms';
import { Auth,NotificationProvider } from '../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';

@IonicPage()
@Component({
  selector: 'page-convenio-calusac-create',
  templateUrl: 'convenio-calusac-create.html',
})
export class ConvenioCalusacCreatePage {

  public form                  : FormGroup;
  username: string = "";
  public nombre                 : any;
  public codigo		     : any;
  public tipo		     : any;
  public fechaini:any;

  public fechafin:any;

  private _ID                  : String;
  public pageTitle           	: string;

  constructor( public storage: Storage,public msj: NotificationProvider, public translateService: TranslateService,public viewCtrl: ViewController,public authService:  Auth,public navCtrl 		: NavController,
    public navParams 	: NavParams,   private _FB          : FormBuilder,         public actionSheetCtrl: ActionSheetController,
          public events: Events)
  {
     // Define form validation - VERY basic!
     this.form = this._FB.group({
       nombre     : ['', Validators.required],
       codigo    : ['', Validators.required],
       fechaini     : ['', Validators.required],
       fechafin     : ['', Validators.required],
       tipo     : ['', Validators.required],

     });


  }

  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  });
  }

  ionViewDidLoad() : void
  {
    if(this.navParams.get("record")==null)
    {


      this.translateService.get('ITEM_CREATE_TITLE').subscribe((value) => {
        this.pageTitle=value;
      });
    }
    else {
       this._ID 				=	this.navParams.data.record._id;
       this.nombre		=	this.navParams.data.record.nombre;
       this.codigo		=	this.navParams.data.record.monto;
       this.fechaini		=	this.navParams.data.record.f1;
       this.tipo		=	this.navParams.data.record.tipo;
       this.fechafin		=	this.navParams.data.record.f2;
       this.translateService.get('ITEM_UPDATE_TITLE').subscribe((value) => {
        this.pageTitle=value;
      });
    }
  }



  manageGallery() : void
  {
  let creat='Actualiza';
    if(this.navParams.get("record")==null)
    {  creat='Crea';
    }
    var monto=0;
    if(this.form.controls['codigo'].value)
    {
      if(this.form.controls['codigo'].value==undefined)
      {}
      else
      {
        monto=this.form.controls['codigo'].value;
      }
    }

     let   options       : any	     = {
       idempresa:this.navParams.data.idempresa,
       categoria:this.navParams.data.idcategoria,
       f1 : this.form.controls['fechaini'].value,
       f2 : this.form.controls['fechafin'].value,
     nombre : this.form.controls['nombre'].value,
     monto: monto,
     tipo: this.form.controls['tipo'].value,
     bitacora:{idempresa:this.navParams.data.idempresa,idafiliado:'',email: this.username,permiso:creat,accion:creat+' Departamento '}
    };


     if(this.navParams.get("record")==null)
     {
          if(options){
            this.authService.createReg('',options,'conveniocalusacs').then((result) => {
              if (!this.form.valid) { return; }
              this.viewCtrl.dismiss(result);
          }, (err) => {
            this.msj.showError3('Error',err.error);
          });
          }
     }
     else
     {
      if(options){
        this.authService.createReg(this._ID,options,'conveniocalusacs').then((result) => {
           if (!this.form.valid) { return; }
        //   console.log(result)
           this.viewCtrl.dismiss(result);
       }, (err) => {
        this.msj.showError3('Error',err.error);
       });
    }}
  }

  cancel() {
    this.viewCtrl.dismiss();
  }
  done() {
    if (!this.form.valid) { return; }
    //this.viewCtrl.dismiss(this.form.value);
  }



}



